<template>
    <div v-if="completed">
        {{ res }}
    </div>
    <loading-small v-else></loading-small>
</template>

<script>

import bonus_recommend from '../../common/es/bonus/bonusApi/bonus_recommend'
import bonus_active_house from '../../common/es/bonus/bonusApi/bonus_active_house'
import bonus_active_layer from '../../common/es/bonus/bonusApi/bonus_active_layer'

import api_bonus from '../../common/es/bonus/bonus'

import LoadingSmall from './LoadingSmall.vue'

    export default {
  components: { LoadingSmall },
        props: {
            user: {
                type: Object
            },
            code: {
                type: String,
            }
        },
        name: '',
        data() {
            return {
                usd_recommend: 0,
                usd_active_house: 0,
                usd_team_layer: 0,

                res: 0,
                completed: false
            }
        },
        async mounted() {
            await this.showRes()
        },
        methods: {
            async showRes() {
                this.completed = false

                if (this.code == 'usd_recommend') {
                    const e = await bonus_recommend(this.user)
                    this.res = e[0] + ', ' + e[1] + ', ' + e[2]
                }
                else if (this.code == 'usd_active_house') {
                    this.res = await bonus_active_house(this.user)
                }
                else if (this.code == 'usd_team_layer') {
                    this.res = await bonus_active_layer(this.user)
                }
                else {
                    this.res = 0
                }

                this.completed = true
            },
            async getUsd() {
                this.usd_recommend = await bonus_recommend(this.user)
                this.usd_active_house = await bonus_active_house(this.user)
                this.usd_team_layer = bonus_active_layer(this.user)

                const bonus = api_bonus.ser_bonus(
                    this.usd_recommend,
                    this.usd_active_house,
                    this.usd_team_layer
                )

                this.user.bonus = bonus

                return bonus
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


