<template>
    <span @click="num" v-if="!loading">
        同步
    </span>

    <span v-else>
        等待
    </span>

</template>

<script>
// 以 Boos 为单位的递归开头，每一个无上线成员均为一名 Boos
// 有多少 Boos 则有多少个递归运算
import api_bonus from '../../common/es/api_bonus/index'

import LoadingSmall from '../Data/LoadingSmall.vue'
    export default {
  components: { LoadingSmall },
        props: {
            user_s: {
                type: Array
            },
            user_e: {
                type: Array
            }
        },
        name: '',
        data() {
            return {
                loading: false,
                bonus: [ ],
                transfer: [ ]
            }
        },
        mounted() {
            this.transfer = this.$store.state.transfer
            this.getBonus()
        },
        methods: {
            
            async getBonus() {
                const b = await this.conn.get(
                    this.api.bonus, 
                    this.$store.state.token,
                    {
                        'chronu': this.$store.state.chronus.id
                    }
                )

                if (b) {
                    this.bonus = b.map(e => {
                        return {
                            id: e.id,
                            user: e.user.id
                        }
                    })
                }
            },

            async netoSave_enroller(user) {
                const data = { usd_recommend: user.bonus.usd_recommend }

                this.bonus.map(e => {
                    if (e.user == user.id) {
                        data.bonus = e.id
                    }
                })
                
                const res = await this.conn.put(
                    this.api.bonus + '/' + data.bonus,
                    this.$store.state.token,
                    data
                )

                if (res) { return true }
                return false
            },
            async netoSave_sponser(user) {
                const data = {
                    ...user.bonus, vip: user.vip.name + '_' + user.vip.star
                }

                this.bonus.map(e => {
                    if (e.user == user.id) { data.bonus = e.id }
                })
                
                const res = await this.conn.put(
                    this.api.bonus + '/' + data.bonus,
                    this.$store.state.token,
                    data
                )

                if (res) { return true }
                return false
            },
            num() {
                this.loading = true
                this.$emit('start_Father')

                
                api_bonus._build(this.user_s, this.transfer, this.netoSave_sponser)

                api_bonus._build(this.user_e, this.transfer, this.netoSave_enroller)

                setTimeout(() => {
                    this.loading = false

                    this.$emit('finished_Father')
                }, 4600)
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


